/*------ Main RTL Rules -------*/
body {
  text-align: right;
  unicode-bidi: embed;
  direction: rtl; }

div, dl, dt, dd, ul, ol, li,
h1, h2, h3, h4, h5, h6, pre, form, p, blockquote, th, td {
  direction: inherit; }

table > thead > tr > th, .table > tbody > tr > th,
table > tfoot > tr > th, .table > thead > tr > td,
table > tbody > tr > td, .table > tfoot > tr > td {
  text-align: right; }

ol ol, ol ul, ul ol, ul ul {
  padding-right: 0; }

ul strong {
  display: inline-block; }

ol li,
ol ol li,
ul ol li {
  padding-left: 0;
  padding-right: 20px; }

ol li::before,
ol ul ol li::before,
ul ol li::before {
  left: auto;
  right: 0; }

ol {
  padding-left: 0;
  padding-right: 20px; }

.list ul {
  padding: 0 60px 0 0; }

ol:not([class]) ul,
ul:not([class]) ul {
  padding: 0 40px 0 0; }

ul:not([class]) {
  padding-left: 0;
  padding-right: 40px; }

ol ul li {
  padding-right: 0; }

/*------ Helpers RTL Rules -------*/
.f-left {
  float: right !important; }

.f-right {
  float: left !important; }

/* =WordPress Core
-------------------------------------------------------------- */
.align-left {
  text-align: right; }

.align-right {
  text-align: left; }

.alignright {
  float: left;
  margin: 5px 40px 20px 0; }

.alignleft {
  float: right;
  margin: 5px 0 20px 20px; }

a img.alignright {
  float: left;
  margin: 5px 40px 20px 0; }

a img.alignleft {
  float: right;
  margin: 5px 0 20px 20px; }

.wp-caption.alignleft {
  margin: 5px 0 20px 20px; }

.wp-caption.alignright {
  margin: 5px 20px 20px 0; }

